body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* variable */
:root {
  --bg-body-color: rgb(141, 141, 141);

  --border-drum-machine-color: rgb(255, 165, 0);
  --bg-drum-machine-color: rgb(179, 179, 179);

  --bg-button-color: rgb(128, 128, 128);
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: rgb(141, 141, 141);
  background-color: var(--bg-body-color);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

#drum-machine {
  background-color: rgb(179, 179, 179);
  background-color: var(--bg-drum-machine-color);
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 70vh;
  border: 0.5rem solid rgb(255, 165, 0);
  border: 0.5rem solid var(--border-drum-machine-color);
}

/* button section start */

.sound-button {
  display: grid;
  grid-template: repeat(3, 1fr) / repeat(3, 1fr);
}

.drum-pad {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  padding: 30%;
  margin: 0.8rem;
  background-color: rgb(128, 128, 128);
  background-color: var(--bg-button-color);
  border-radius: 15%;
  border: none;
  box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
  cursor: pointer;
  transition: 0.5s;
  font-weight: bold;
}

.drum-pad:hover {
  -webkit-transform: translate(0, -0.5rem);
          transform: translate(0, -0.5rem);
}

/* button section end */
